.bike-item {
  position: relative;
  z-index: 1;
  text-align: center;
  width: fit-content;
}

.bike-item img {
  border-radius: 10px;
  background-color: $color-nero;

  @include vp--1023 {
    width: 270px;
    height: 300px;
  }
}

.bike-item:nth-child(3n+1) {
  padding-top: 10px;

  @include vp--767 {
    padding: 0;
  }

  &::before {
    content: "";
    border: 27px solid $color-robins-egg-blue;
    border-radius: 50%;
    width: 170px;
    height: 170px;
    position: absolute;
    top: 172px;
    left: -62px;
    z-index: -1;

    @include vp--1023 {
      border: 14px solid $color-robins-egg-blue;
      width: 121px;
      height: 121px;
      top: 212px;
      left: -33px;
    }

    @include vp--767 {
      top: 199px;
      left: -24px;
      width: 127px;
      height: 127px;
      background: none;
      border: 18px solid $color-robins-egg-blue;
      border-radius: 50%;
    }
  }
}

.bike-item:nth-child(3n+2) {
  padding-top: 40px;

  @include vp--1023 {
    padding-top: 10px;
  }

  @include vp--767 {
    padding: 0;
  }

  &::before,
  &::after {
    content: "";
    width: 47px;
    height: 47px;
    background-image: url("../img/desktop-square-icon.svg");
    position: absolute;
    z-index: 1;

    @include vp--1023 {
      display: none;
    }
  }

  &::before {
    top: 21px;
    left: -17px;
  }

  &::after {
    bottom: 30px;
    right: -21px;
  }
}

.bike-item:nth-child(3n+3) {
  &::before {
    content: "";
    background-image: url("../img/desktop-round-icon.svg");
    background-repeat: no-repeat;
    background-size: cover;
    width: 95px;
    height: 95px;
    position: absolute;
    top: -33px;
    right: -37px;
    z-index: 1;

    @include vp--1023 {
      width: 68px;
      height: 68px;
      top: -20px;
      right: -24px;
    }

    @include vp--767 {
      top: -21px;
      right: -15px;
    }
  }
}


.bike-item__title {
  font-weight: 600;
  font-size: 22px;
  line-height: 34px;
  text-align: center;
  margin: 17px 0 0;
  letter-spacing: -0.5px;
  transition: color $transition-base;

  @include vp--1023 {
    margin: 14px 0 0;
  }

  @include vp--767 {
    margin: 17px 0 0;
  }
}

.bike-item__link {
  display: block;
  color: inherit;
  transition: transform $transition-base;

  &:hover {
    transform: scale(1.02);
    color: $color-robins-egg-blue;
    transition: transform $transition-base, color $transition-base;
  }
}
